 *{
     padding: 0;
     margin: 0;
     font-family: "Jost", sans-serif
 }

 body {
     background:rgb(55, 54, 66);
     display: flex;
     justify-content: center;
     align-items: center;
     flex-wrap: wrap;
     height: 100vh;
     width: 100vw;
     font-family: "Jost",sans-serif;
 }

 html {
     font-size: 100%;
     height: 100%;
     background: #eee;
 }
 
 .box {
     width: 25vw;
     height: 60vh;
     border-radius: 0.5rem;
     box-shadow: 0 0.2rem 3rem rgba(0,0,0,0.2);
     background: #a5bbdd;
     position: relative;
     overflow: hidden;
     transform: translate3d(0,0,0);
     min-width: 20rem;
     min-height: 35rem;
 }  

 .wave {
     opacity: 0.3;
     position: absolute;
     top: 120%;
     left: 50%;
     background: white;
     width: 50rem;
     height: 50rem;
     margin-left: -25rem;
     margin-top: -20rem;
     transform-origin: 50% 48%;
     border-radius: 43%;
     animation: drift 3000ms infinite linear;
     z-index: 1;
  }

  .wave-three{
      animation: drift 5000ms infinite linear;
      z-index: 2 im !important;
      opacity: 0.2;
  }

  .wave-three{
    animation: drift 5000ms infinite linear;
    z-index: 3 im !important;
    opacity: 0.1;
 }

.box:after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
 }

   

 #date {
     text-align: center;
     margin-top: 0.5rem;
     color: #57606f;
     font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
 }

 .temp {
     margin-top: 2.5rem;
     text-align: center;
     font-size: 3rem;
 }

 .tempmin_max {
     text-align: center;
     margin-top: 0.3rem;
     font-weight: 300;
     font-size: 1.2rem;
     color: #57606f;
 }

 #ewathercon {
     height: 55%;
     display: flex;
     justify-content: center;
     align-items: center;
     font-size: 3rem;
 }

 #weather .fas {
     font-size: 6rem;
     animation: fas-anime 3s linear infinite alternate;
 }

 @keyframes fas-anime {
     from{
         transform: scale(1.5);
     }
 }

 @media (max-width:600px){
     .box{
         width: 90vw;
         height: 80vh;
     }
 }

 .wave {
     top: 85%;
 }

 #weathercon {
     font-size: 5rem;
 }

 .info {
     font-size: 1.5rem;
 }

 @media (max-height:500px){
    .box{
         height: 80vh;
    }
}

.wave {
    top: 115%;
}

 body > span {
     width: 100vw;
     text-align: center;
     color: grey;
 }

 .inputData {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
 }

 .inputFeild {
     width: 50%;
     margin: auto;
     border-radius: 5rem;
     padding: 1 rem 1 rem ;
     outline: none;
     font-size: 1.5 rem;
 }

 .errMsg {
     text-align: center;
     font-size: 2 rem;
     margin: 2rem auto;
 }